.content-section {
  height: 90vh;
}

.color-primary {
  color: #2b2b2b;
  font-weight: 600;
  font-size: 54px;
}

.form-label {
  font-size: 12px;
  font-weight: 400;
}

.button-primary {
  background-color: #ee6639 !important;
  color: #ffffff;
  border: none;
  border-radius: 100px;
  padding: 0.5rem 3rem;
  font-weight: 500;
  font-size: 18px;
}

.color-dark-grey {
  color: #707070;
}

.button-green {
  background-color: #04d976;
  color: #ffffff;
  border: none;
}

.button-layout {
  padding: 0.5rem 2rem;
  font-weight: 500;
}
.button-contained,
.button-contained:hover {
  background-color: #2d3663;
  color: white;
}

.button-outlined {
  color: #04d976;
  border: 1px solid #04d976;
  background-color: transparent;
}

.button-a-tag {
  color: #1681f0;
  background-color: transparent;
  border: none;
  font-weight: 500;
  font-size: 14px;
  text-decoration: none;
}

.button-outlined-q {
  color: #48b27e;
  border: 1px solid #48b27e;
  background-color: transparent;
}

.button-virtual {
  background-color: transparent;
  border: none;
}

.button-virtual:hover {
  color: rgb(71, 134, 244);
}
.button-browse {
  background-color: #0fc6d9;
  color: #ffffff;
  border-radius: none;
  border: none;
}

.fw-b {
  font-weight: bold;
}
.fw-mb {
  font-weight: 600;
}
.fw-500 {
  font-weight: 500;
}
.fs-18 {
  font-size: 1.2em;
}

.fs-16 {
  font-size: 1em;
}

.fs-20 {
  font-size: 1.4;
}

.fs-22 {
  font-size: 22px;
}

.fs-24 {
  font-size: 24px;
}
.fs-28 {
  font-size: 1.5em;
}

.fs-32 {
  font-size: 1.8em;
}

.fs-36 {
  font-size: 36px;
}

.fs-56 {
  font-size: 3.5em;
}

.fs-64 {
  font-size: 64px;
}

.fs-14 {
  font-size: 14px;
  font-family: Helvetica;
  font-weight: 500;
}

.fs-12 {
  font-size: 12px;
  font-family: Helvetica;
  font-weight: 400;
}
.a-tag-btn {
  color: #04d976;
  text-decoration: none;
}
.a-tag-btn:hover {
  color: #04d976;
}

.a-tag-white-btn {
  color: #000000;
  background-color: #ffffff;
  text-decoration: none;
}

.a-tag-white-btn:hover {
  color: #000000;
}

.a-tag-common {
  text-decoration: none;
}

.a-tag {
  color: #029c9a;
  text-decoration: none;
  font-size: 16px;
}

.btn-check + .radio-button:hover {
  background-color: #acf2d2;
  color: #2b3566;
}

.btn-check:checked + .radio-button {
  background-color: #48b27e;
  color: white;
}

.link-class {
  text-decoration: none;
}
.link-class:hover {
  color: white;
}

.input-class {
  border: 1px solid #bcbcbc;
  background-color: transparent;
  border-radius: 0;
}

input.form-control:focus {
  box-shadow: none;
  border: 1px solid #0fc6d9;
}

.radio-btn-label {
  color: #2b3566;
  font-size: 24px;
}

.close-icon {
  position: absolute;
  top: 45px;
  right: 45px;
}

.page-header {
  font-size: 40px;
  font-weight: 600;
}

.bg-blue {
  background-color: #0fc6d9;
  color: #ffffff;
  font-weight: bold;
}

.bg-pink {
  background-color: #f230bb;
  color: #ffffff;
  font-weight: bold;
}

.pink {
  color: #f230bb;
}

.bg-green {
  background-color: #04d976;
  color: #ffffff;
}

.heading-shadow {
  text-shadow: 0 3px 5px #00000029;
}

.container-shadow {
  box-shadow: 0 3px 5px #00000029;
}

.dim-gray {
  color: #6c6c6c;
}

.dark-blue {
  color: #2b3566;
}
.radio-btn {
  border: 3px solid #00cfe5;
  color: #ffffff;
}

.participants-btn {
  color: #ffffff;
  background-color: #1f376d;
  border: none;
}

.records-card {
  border: 1px solid #dfe1e6;
  border-radius: 4px;
}

.dashboard-primary-color {
  color: #172e50;
}
